ight=\"50px\"\r\n                 style=\" vertical-align:middle;\" />\r\n          </template>\r\n\r\n        </el-table-column>\r\n        <el-table-column label=\"轮播内容\"\r\n                         align=\"center\">\r\n\r\n          <template slot-scope=\"scope\">\r\n            <el-popover placement=\"top-start\"\r\n                        title=\"\"\r\n                        width=\"200\"\r\n                        trigger=\"hover\"\r\n                        :content=\"scope.row.content\">\r\n              <el-button type=\"text\"\r\n                         slot=\"reference\"\r\n                         class=\"ellipsis\">{{scope.row.content}}</el-button>\r\n            </el-popover>\r\n          </template>\r\n\r\n        </el-table-column>\r\n        <el-table-column label=\"轮播状态\"\r\n                         align=\"center\">\r\n          <template slot-scope=\"scope\">\r\n            {{scope.row.status==1 ? '显示' : '隐藏'}}\r\n          </template>\r\n        </el-table-column>\r\n        <el-table-column label=\"审核人\"\r\n                         align=\"center\"\r\n                         prop=\"audit_person\">\r\n\r\n        </el-table-column>\r\n        <el-table-column label=\"驳回理由\"\r\n                         align=\"center\">\r\n\r\n          <template slot-scope=\"scope\">\r\n            <el-popover placement=\"top-start\"\r\n                        title=\"\"\r\n                        width=\"200\"\r\n                        trigger=\"hover\"\r\n                        :content=\"scope.row.reason\">\r\n              <el-button type=\"text\"\r\n                         slot=\"reference\"\r\n                         class=\"ellipsis\">{{scope.row.reason}}</el-button>\r\n            </el-popover>\r\n          </template>\r\n\r\n        </el-table-column>\r\n        <el-table-column label=\"驳回时间\"\r\n                         align=\"center\"\r\n                         prop=\"audit_time\">\r\n          <template slot-scope=\"scope\">\r\n            {{scope.row.audit_time | datetime}}\r\n          </template>\r\n        </el-table-column>\r\n\r\n      </el-table>\r\n\r\n      <!-- 分页 -->\r\n      <div class=\"page_center\"\r\n           v-show=\"list && list.length>0\">\r\n        <paging :page-count=\"pageCount\"\r\n                :page=\"page\"\r\n                @index=\"paging\"></paging>\r\n        <!--分页的组件-->\r\n      </div>\r\n    </div>\r\n\r\n  </div>\r\n</template>\r\n<script type=\"text/ecmascript-6\">\r\n\r\nexport default {\r\n  data () {\r\n    return {\r\n      list: [],\r\n      token: window.sessionStorage.getItem('bbytoken'),\r\n      page: 1,\r\n      pageCount: 1,\r\n      thCurId: '',\r\n      authList: [],\r\n      threeAuthList: [],\r\n\r\n    }\r\n  },\r\n\r\n  methods: {\r\n    paging (e) {\r\n      this.page = e;\r\n      this.init()\r\n    },\r\n\r\n    init () {\r\n      this.$axios.post('admin/DeTravel/bannerList', { token: this.token, page: this.page, audit_status: 2 })\r\n        .then(res => {\r\n          this.list = res.data.data.list;\r\n          this.pageCount = res.data.data.rows;\r\n        }).catch(err => { })\r\n    },\r\n\r\n\r\n\r\n\r\n    Auth () {\r\n      var id = this.$route.query.id;\r\n      this.curId = id;\r\n      this.$axios.post('admin/Auth/erAuth', {\r\n        token: window.sessionStorage.getItem('bbytoken'),\r\n        id: id\r\n      })\r\n        .then(res => {\r\n\r\n          if (res.data.code == 1) {\r\n            var arr = res.data.data;\r\n            for (var i = 0; i < arr.length; i++) {\r\n              if (arr[i].son) {\r\n                if (arr[i].name == '轮播列表') {\r\n                  this.seCurId = arr[i].id;\r\n                  this.threeAuthList = arr[i].son;\r\n                }\r\n                for (var j = 0; j < arr[i].son.length; j++) {\r\n                  if (arr[i].action != arr[i].son[j].action) {\r\n                    arr[i].action = arr[i].son[0].action;\r\n                  }\r\n                  if (arr[i].son[j].name == '已驳回' && arr[i].name == '轮播列表') {\r\n                    this.thCurId = arr[i].son[j].id;\r\n                  }\r\n                }\r\n              }\r\n            }\r\n            this.authList = arr;\r\n          } else {\r\n            this.$alert(res.data.msg, '提示', { type: 'error' });\r\n          }\r\n        })\r\n        .catch(err => {\r\n          alert(err);\r\n        })\r\n    }\r\n  },\r\n  mounted () {\r\n    this.Auth()\r\n    this.init()\r\n  },\r\n}\r\n</script>\r\n<style scoped>\r\n.ellipsis {\r\n  width: 100px;\r\n  overflow: hidden;\r\n  text-overflow: ellipsis;\r\n  display: -webkit-box;\r\n  -webkit-box-orient: vertical;\r\n  -webkit-line-clamp: 2;\r\n  margin: 0 auto;\r\n  text-align: center;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);

